#homepage {
    height: 92vh;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: space-between;
}

.homeHalf {
    width: 100%;
    display: flex;
    flex-flow: column;
}

#homeSlant {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 50vh;
    background-color: #1b2c3a;
    -webkit-animation: homeSlant 4s ease-out .5s forwards;
            animation: homeSlant 4s ease-out .5s forwards;
}

@-webkit-keyframes homeSlant {
    to {
        -webkit-transform: skewY(9deg);
                transform: skewY(9deg);
    }
}

@keyframes homeSlant {
    to {
        -webkit-transform: skewY(9deg);
                transform: skewY(9deg);
    }
}

#homeLogo {
    height: 38%;
    align-items: center;
    justify-content: flex-end;
}

#homeLogoIcon {
    width: 10em;
    opacity: 0;
    -webkit-animation: rotateLogo 4s ease-out .5s forwards;
            animation: rotateLogo 4s ease-out .5s forwards;
}

@-webkit-keyframes rotateLogo {
    0% {
        opacity: 0;
        -webkit-transform: scale(.25) rotate(0deg);
                transform: scale(.25) rotate(0deg);
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 1;
        -webkit-transform: scale(1) rotate(180deg);
                transform: scale(1) rotate(180deg);
    }
}

@keyframes rotateLogo {
    0% {
        opacity: 0;
        -webkit-transform: scale(.25) rotate(0deg);
                transform: scale(.25) rotate(0deg);
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 1;
        -webkit-transform: scale(1) rotate(180deg);
                transform: scale(1) rotate(180deg);
    }
}

#homeWords {
    height: 40%;
    padding: 7vh 10vw 0;
    align-items: center;
    justify-content: flex-start;
    opacity: 0;
    -webkit-animation: wordAppearVert 4s .5s forwards;
            animation: wordAppearVert 4s .5s forwards;
}

@-webkit-keyframes wordAppearVert {
    from {
        opacity: 0;
        -webkit-transform: translateY(-20vh);
                transform: translateY(-20vh);
    }
    to {
        opacity: 1;
        -webkit-transform: translateY(0vh);
                transform: translateY(0vh);
    }
}

@keyframes wordAppearVert {
    from {
        opacity: 0;
        -webkit-transform: translateY(-20vh);
                transform: translateY(-20vh);
    }
    to {
        opacity: 1;
        -webkit-transform: translateY(0vh);
                transform: translateY(0vh);
    }
}

.homeTagline {
    font-size: 100%;
    line-height: 1.5;
    font-weight: bold;
    font-family: 'Codystar', cursive;
}

#dspTag {
    /* margin-left: -1em; */
    margin-bottom: 1em;
}


