.slantTitle {
    height: 5vh;
    padding-left: 20vw;
    font-size: 140%;
}

.slantInfoWrapper {
    padding: 0 10vw 0 26vw;
}

.slantUp {
    margin: 15vh 0;
    padding: 10vh 0;
    width: 100%;
    -webkit-animation: slantUp 2s ease-out .5s forwards;
            animation: slantUp 2s ease-out .5s forwards;
}

@-webkit-keyframes slantUp {
    to {
        -webkit-transform: skewY(-9deg);
                transform: skewY(-9deg);
    }
}

@keyframes slantUp {
    to {
        -webkit-transform: skewY(-9deg);
                transform: skewY(-9deg);
    }
}

.unSkew {
    -webkit-animation: unSkewUp 2s ease-out .5s forwards;
            animation: unSkewUp 2s ease-out .5s forwards;
}

@-webkit-keyframes unSkewUp {
    to {
        -webkit-transform: skewY(9deg);
                transform: skewY(9deg);
    }
}

@keyframes unSkewUp {
    to {
        -webkit-transform: skewY(9deg);
                transform: skewY(9deg);
    }
}

.slantDown {
    margin: 15vh 0;
    padding: 10vh 0;
    width: 100%;
    -webkit-animation: slantDown 2s ease-out .5s forwards;
            animation: slantDown 2s ease-out .5s forwards;
}

@-webkit-keyframes slantDown {
    to {
        -webkit-transform: skewY(9deg);
                transform: skewY(9deg);
    }
}

@keyframes slantDown {
    to {
        -webkit-transform: skewY(9deg);
                transform: skewY(9deg);
    }
}

.unSkewDown {
    -webkit-animation: unSkewDown 2s ease-out .5s forwards;
            animation: unSkewDown 2s ease-out .5s forwards;
}

@-webkit-keyframes unSkewDown {
    to {
        -webkit-transform: skewY(-9deg);
                transform: skewY(-9deg);
    }
}

@keyframes unSkewDown {
    to {
        -webkit-transform: skewY(-9deg);
                transform: skewY(-9deg);
    }
}

