@media (min-width: 43em) {
    .slantUp {
        margin: 10vh 0;
        -webkit-animation: largeSlantUp 2s ease-out .5s forwards;
                animation: largeSlantUp 2s ease-out .5s forwards;
    }

    @-webkit-keyframes largeSlantUp {
        to {
            -webkit-transform: skewY(-3deg);
                    transform: skewY(-3deg);
        }
    }

    @keyframes largeSlantUp {
        to {
            -webkit-transform: skewY(-3deg);
                    transform: skewY(-3deg);
        }
    }

    .unSkew {
        -webkit-animation: largeUnSkewUp 2s ease-out .5s forwards;
                animation: largeUnSkewUp 2s ease-out .5s forwards;
    }

    @-webkit-keyframes largeUnSkewUp {
        to {
            -webkit-transform: skewY(3deg);
                    transform: skewY(3deg);
        }
    }

    @keyframes largeUnSkewUp {
        to {
            -webkit-transform: skewY(3deg);
                    transform: skewY(3deg);
        }
    }

    .slantDown {
        margin: 10vh 0;
        -webkit-animation: largeSlantDown 2s ease-out .5s forwards;
                animation: largeSlantDown 2s ease-out .5s forwards;
    }

    @-webkit-keyframes largeSlantDown {
        to {
            -webkit-transform: skewY(3deg);
                    transform: skewY(3deg);
        }
    }

    @keyframes largeSlantDown {
        to {
            -webkit-transform: skewY(3deg);
                    transform: skewY(3deg);
        }
    }

    .unSkewDown {
        -webkit-animation: largeUnSkewDown 2s ease-out .5s forwards;
                animation: largeUnSkewDown 2s ease-out .5s forwards;
    }

    @-webkit-keyframes largeUnSkewDown {
        to {
            -webkit-transform: skewY(-3deg);
                    transform: skewY(-3deg);
        }
    }

    @keyframes largeUnSkewDown {
        to {
            -webkit-transform: skewY(-3deg);
                    transform: skewY(-3deg);
        }
    }
}

@media (min-width: 50em) { 
    .slantTitle {
        height: 6vh;
        padding-left: 25.5vw;
        font-size: 160%;
    }
    
    .slantInfoWrapper {
        padding: 0 15vw 0 30vw;
    }

}